@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
    display: flex;
    flex-direction: column;
    min-width: 1000px;
    min-height: 100vh;

    text-align: center;
}

.app-header {
    display: flex;
    align-items: flex-start;
    justify-content: center;

    padding-top: 16px;
    padding-bottom: 16px;

    background-color: #ffdb91;
    color: black;
}

.left-title {
    margin-top: auto;
    margin-bottom: auto;
    min-width: 280px;

    font-family: 'Balsamiq Sans', sans-serif;
    font-size: calc(14px + 3vmin);
}

.description {
    margin-top: 10px;

    font-size: calc(12px + 1vmin);
    text-align: justify;
}

.lang-button {
    opacity: 0.7;
    transition: 200ms all ease-in;

    border: none;
    background: transparent;

    font-size: calc(16px + 1vmin);
}

.lang-button:hover {
    opacity: 1;
}

.main-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1000px;

    margin: auto;
}

.main-text {
    margin-top: 10px;

    font-size: calc(10px + 1vmin);
}

.charts-block {
    margin-top: 10px;
    margin-bottom: 10px;
}

.charts-names {
    margin-top: 20px;

    font-family: 'Balsamiq Sans', sans-serif;
    font-size: calc(10px + 2vmin);
}

.footer-block {
    margin-top: auto;
    border-top: 2px solid #f5e5bc;
    padding-bottom: 12px;
    padding-top: 12px;

    color: #504a4a;
    font-family: 'Balsamiq Sans', sans-serif;
    font-size: 14px;
}

.gh-logo {
    fill: #828282;
    transition: 0.2s;
}

.gh-logo:hover {
    fill: #504a4a;
    transition: 0.2s;
}

